<script>
    import { selectedShard } from './store.js';

    export let id;
    export let status;
    export let margin;

    function setSelectedShard(id) {
        if (typeof id === 'number') {
            selectedShard.set(id);
        }
    }
</script>

<div
    class="shard"
    data-margin={margin}
    data-status={status}
    on:click={() => setSelectedShard(id)}
>
    <div>{id}</div>
</div>

